body {
  --container-width: 1700px;
  --white: #ffffff;
  --black: #000000;
  --background: #fcfcfc;
  --theme: #ffe168;
  --theme-light: #ffeb9c;
  --theme-dark: #dfc040;
  --red: #ff0000;
  --border-color: #bdbdbd;
  --gray: #444348;
  --gray-light: #8f8f91;
  --gray-lightest: #b4b4b6;
  --gray-lightest-more: #dbdbdb;
  --section-gap: 50px;
  --grid-background: #a3a3a3;
}

@function flexWidth($count, $gap) {
  @return calc(100% / $count - (($count - 1) * $gap) / $count);
}

@mixin flexContainer($gap, $items) {
  display: flex;
  flex-wrap: wrap;
  --gap: #{$gap};
  --items: #{$items};
  gap: var(--gap);
}

@mixin flexItem {
  flex-shrink: 0;
  --width: calc(
    100% / var(--items) - (var(--items) - 1) * var(--gap) / var(--items)
  );
  width: var(--width);
}

